<template>
  <div class="content_detail" v-if="result!==null" ref="scroll">
    <div v-show="!isShow">
      <div class="tag">
        <div class="img">
          <img class="poster" :src="result.poster">
          <img class="back" @click="back"
               src="data:image/png;base64,">
          <div :class="['header',isFixed?'header_b':'']">{{ result.name }}</div>
        </div>
        <div class="value_detail">
          <p class="title">{{ result.name }}<span class="quality">{{ result.filmType.name }}</span> <span
            class="grade" v-if="result.grade">{{ result.grade }} <span class="grade_text">分</span></span></p>
          <p>{{ result.category | str }}</p>
          <p>{{ result.premiereAt | data }}</p>
          <p>{{ result.nation }} | {{ result.runtime }}分钟</p>
          <div :class="['value',value]" ref="box">
            <!-- 26 -->
            <p ref="text">{{ result.synopsis }}</p>
          </div>
          <div class="zk">
            <img src="zk.png" @click="zhankai" :class="value!=='value_hidden'?'rollback':''">
          </div>
        </div>
        <!-- 演职人员 -->
        <div class="members">
          <p class="title">演职人员</p>
          <div class="list">
            <dl v-for="value in result.actors">
              <dt><img :src="value.avatarAddress"></dt>
              <dd>
                <p class="p1">{{ value.name }}</p>
                <p class="p2">{{ value.role }}</p>
              </dd>
            </dl>
          </div>
        </div>
        <div :class="result.isSale?'still':'still stillactive'">
          <p class="title">
            <span>剧照</span>
            <span v-if="result.photos" class="span2" @click="isShow=true">全部({{ result.photos.length }})
						<i class="iconfont">&#xe643;</i>
					</span>
          </p>
          <div class="img">
            <div v-for="(value,index) in result.photos" class="img_img">
              <img :src="value" alt="" @click="prevPosition(index)">
            </div>
          </div>
          <div class="no" v-if="!result.photos">暂无电影剧照</div>
        </div>
      </div>
      <div class="xzgp" v-if="result.isSale">选座购票</div>
    </div>
    <div class="photos" v-show="isShow">
      <div class="img">
        <img class="back" @click="isShow=false"
             src="data:image/png;base64,">
        <div class="header_pto" v-if="result.photos">剧照({{ result.photos.length }})</div>
      </div>
      <div class="img_group">
        <div v-for="(value,index) in result.photos" class="img_">
          <img :src="value" alt="" @click="prevPosition(index)">
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import Vue from "vue";
  import {ImagePreview, Dialog, Toast} from 'vant';


  // 全局注册
  Vue.use(Dialog);
  Vue.use(Toast);
  Vue.use(ImagePreview);

  Vue.filter("str", value => {
    return value.replaceAll("|", " | ")
  })
  Vue.filter("data", value => {
    let d = new Date(value * 1000)
    return d.getFullYear() + "-" + (d.getMonth() + 1) + "-" + d.getDate() + "上映";
  })
  export default {
    data() {
      return {
        result: null,
        value: "value_hidden",
        isFixed: false,
        isShow: false,
      }
    },
    mounted() {
      let filmid = this.$route.params.id;
      if (filmid) {
        Toast.loading({
          message: 'loading...',
          forbidClick: true,
          duration: 0
        });
        fetch(`https://m.maizuo.com/gateway?filmId=${filmid}&k=1860256`, {
          method: "GET",
          headers: {
            'X-Client-Info': '{"a":"3000","ch":"1002","v":"5.0.4","e":"16352477313746499972300801","bc":"210300"}',
            'X-Host': 'mall.film-ticket.film.info'
          }
        })
          .then(value => value.json())
          .then(value => {
            this.result = value.data.film
            window.addEventListener('scroll', this.handleScroll)
            Toast.clear();
          })
      }
      this.$watch(() => {
        return this.result
      }, () => {
        if (!this.result.isSale) {
          this.issale()
        }
      })
    },
    updated() {
      this.$refs.box.style.height = Math.ceil(this.$refs.text.clientHeight)+10 + "px";
    },
    methods: {
      prevPosition(index) {
        ImagePreview({
          images: this.result.photos,
          startPosition: index,
          closeable: true,
          closeIconPosition: "top-left",
          closeIcon: "cross",
        });
      },
      issale() {
        Dialog.confirm({
          title: '暂无排期',
          message: '该影片目前没有排期，到首页看其他电影吧',
          cancelButtonText: "拒绝",
          confirmButtonText: "同意"
        })
          .then(() => {
            this.$router.push('/main')
          })
          .catch(() => {

          });
      },
      back() {
        this.$router.back()
      },
      zhankai() {
        this.value = this.value === "value_hidden" ? "" : "value_hidden"
      },
      handleScroll() {
        let scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body
          .scrollTop // 滚动条偏移量
        let offsetTop = this.$refs.scroll.offsetTop; // 要滚动到顶部吸附的元素的偏移量
        this.isFixed = scrollTop > offsetTop + 44 ? true : false; // 如果滚动到了预定位置，this.isFixed就为true，否则为false
      }
    },
    beforeDestroy() {
      window.removeEventListener('scroll', this.handleScroll)
    }
  }
</script>

<style scoped lang="scss">
  .content_detail {
    background-color: #ededed;

    //剧照大图
    .photos {
      position: fixed;
      width: 100%;
      height: 100%;
      background-color: #ffffff;
      z-index: 1000;

      .img {
        height: 2.8125rem;
        width: 100%;
        background-color: #fff;
        display: flex;
        justify-content: space-between;
        font-size: 1.0625rem;

        .back {
          position: absolute;
          top: 0.375rem;
        }
      }

      .header_pto {
        text-align: center;
        width: 100%;
      }

      .img_group {
        display: flex;
        flex-wrap: wrap;

        div {
          width: calc(100% / 3 - 3px);
          height: 32vw;
          background-color: rgb(249, 249, 249);
          margin: 1px;

          img {
            width: 100%;
            height: 32vw;
          }
        }
      }
    }

    //选座购票
    .xzgp {
      width: 100%;
      position: fixed;
      bottom: 0;
      height: 3.125rem;
      background-color: #ff5f16;
      color: #fff;
      display: flex;
      align-items: center;
      justify-content: center;
    }

    // 剧照
    .stillactive {
      margin-bottom: 0 !important;
    }

    .still {
      background-color: #fff;
      margin-top: 0.9375rem;
      margin-bottom: 65px;
      position: relative;

      .no {
        width: 100%;
        display: flex;
        overflow-x: auto;
        height: 7.1875rem;
        position: absolute;
        top: 3.1875rem;
        justify-content: center;
        padding-top: 1.25rem;
        box-sizing: content-box;
        color: #bdc0c5;
        font-size: 0.875rem;
        box-sizing: border-box;
      }

      .title {
        padding: 15px;
        display: flex;
        justify-content: space-between;

        .span2 {
          display: flex;
          color: #797d82;
          font-size: 0.8125rem;
          align-items: center;

          .iconfont {
            width: 0.8125rem;
            height: 0.8125rem;
            display: flex;
            align-items: center;
            font-size: 0.8125rem;
            line-height: 0.8125rem;
            margin-top: 0.0625rem;
          }

        }
      }

      .img {
        display: flex;
        overflow-x: auto;
        padding: 0 0.1875rem 0.9375rem 0.9375rem;
        height: 6.25rem;

        .img_img {
          flex: 0 0 auto;
          width: 9.375rem;
          height: 6.25rem;
          overflow: hidden;
          display: flex;
          align-items: center;
          margin-right: 0.75rem;
          background-color: rgb(249, 249, 249);
        }

        img {
          width: 100%;
          height: auto;
        }
      }

      .img::-webkit-scrollbar {
        display: none
      }

    }

    // 演职人员
    .members {
      width: 100%;
      height: 14.375rem;
      margin-top: 0.625rem;
      background-color: #fff;

      .title {
        padding: 15px;
      }

      .list {
        overflow-x: auto;
        background-color: #fff;
        display: flex;
        padding: 0 0.9375rem 0 0.1875rem;

        dl {
          margin-left: 0.75rem;

          dt {
            width: 5.3125rem;
            height: 6.25rem;
            overflow: hidden;
            display: flex;
            align-items: center;

            img {
              width: 5.3125rem;
            }
          }

          dd {
            margin-top: 5px;

            .p1 {
              color: #191a1b;
              width: 5.3125rem;
              text-align: center;
              font-size: 0.75rem;
              white-space: nowrap;
              text-overflow: ellipsis;
              overflow: hidden;
            }

            .p2 {
              text-align: center;
              font-size: 10px;
              color: #797d82;
            }
          }
        }
      }

      .list::-webkit-scrollbar {
        display: none
      }
    }

    .img {
      position: relative;
      height: 13.125rem;
      overflow: hidden;
      display: flex;
      align-items: center;

      .header {
        width: 100%;
        background-color: #fff;
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 1.0625rem;
        height: 2.75rem;
        opacity: 0;
        transition: all 200ms ease-in-out;
        position: fixed;
        top: 0;
        border-bottom: 0.0625rem solid #ededed;
        z-index: 1000;
      }

      .header_b {
        opacity: 1;
      }

      .back {
        position: fixed;
        width: 1.875rem;
        height: 1.875rem;
        top: 0.375rem;
        left: 5px;
        z-index: 1001;
      }

      .poster {
        width: 100%;
      }
    }

    .value_detail {
      padding: 12px 15px 15px;
      background-color: #fff;

      .zk {
        text-align: center;

        img {
          width: 0.5rem;
        }
      }

      .rollback {
        width: 0.5rem;
        transform: rotateX(180deg);
      }

      p {
        font-size: 13px;
        color: #797d82;
        margin-top: 4px;
      }

      .value {
        font-size: 13px;
        color: #797d82;
        margin-top: 12px;
        overflow: hidden;
        transition: all 600ms ease-in-out;
      }

      .value_hidden {
        height: 2.25rem !important;
      }

      .title {
        width: 100%;
        color: #191a1b;
        font-size: 18px;
        height: 1.5rem;
        line-height: 1.5rem;
        position: relative;

        .quality {
          font-size: 9px;
          color: #fff;
          background-color: #d2d6dc;
          height: 14px;
          line-height: 14px;
          padding: 0 2px;
          border-radius: 2px;
          display: inline-block;
          position: absolute;
          top: 5px;
          margin-left: 7px;
        }

        .grade {
          position: absolute;
          top: -3px;
          right: 12px;
          font-size: 1.125rem;
          font-style: italic;
          color: #ffb232;

          .grade_text {
            font-size: 0.625rem;
            font-style: normal;
            position: absolute;
            right: -12px;
            top: 2px;
          }
        }
      }
    }
  }
</style>
